body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#navigation{
    display:flex;
    width:100%;
    background-color:#5B7D76;
    height:50px;
}

#navigationItemsLeft{
    display:flex;
    align-items: center;
    margin-left:15%;


}

#navigationList{
    list-style: none;
}

.navItem{
    display:inline-block;
    padding-left:10px;
    color: #ededed;

}

.navItem a:hover{
    color:#1e404e;
}

.navItem a{
    color: #ededed;
    text-decoration:none;
}


.dropDown{
    background-color:#5B7D76;
    display:none;
    padding-top:17px;
    list-style: none;
    padding-left:5px;
    padding-right:5px;
    /* box-shadow: 5px 10px 15px black; */

}

.dropDown div{
    padding:10px;
    text-align: left;
}

.subcategory{
    display:block;
    font-size:1em;
    font-weight:bold;
    padding-bottom:5px;
    color:#80e4fb;
}
.dropDown div li{
    font-size:0.9em;
}


#productDrop:hover .dropDown{
    display:flex;
    position:absolute;
    padding-left:10px;
    width:300px;
}
.logoHolder{
    height:130px;
    background-color:#1E404E;
}

#logoImage{
    height:180px;
    border-radius:90px;

}
#introContainer{
    width:70%;
    margin:0 auto;
}

#introText{
    line-height: 1.4;
}
#shopCategoryContainer{
    width:70%;
    margin:0 auto;
}

#shopCategories{
    display:flex;
    flex-direction: row;
    justify-content: center;
}

.shopCategory{
    position:relative;
    width:30%;
    height:200px;
    margin:15px;    
    background-image: url('https://images.unsplash.com/photo-1533382433768-3d403240fa40?ixlib=rb-0.3.5&s=bd7a3d5a68e7aab801aafd691c0434ca&auto=format&fit=crop&w=300&q=80');
    background-position: center;
    background-size:cover;
    /* border-radius:10px; */
}


.categoryTitle{
    height:100%;
    /* background-color:rgba(0,0,0,0.3);     */
    /* border-radius:10px; */
}

.categoryTitle img{
    display:block;
    width:100%;
    height:100%;
}

.viewButton{
    position:absolute;
    left:0;
    bottom:0;
    width:100%;
    height:25px;
    color:ivory;
    background-color:rgba(0,0,0,0.5);
    border:#5b7d76;
}

.viewButton span{
    color:white;
    font-size:1.4em;
}


#seperationBanner{
    position:relative;
    /* background-image:linear-gradient(#15303b,#172f39,#152d37,#142a35,#132730,#11242b,#10232a);
    background-attachment: fixed;
    height:312px; */
    margin-top:30px;
    margin-bottom:30px;
   
}

#seperationBannerInner{
    background-image: url(/static/media/banner.2066997b.png);
    background-position: center;
    background-repeat: no-repeat;
    height:312px;
    width:100%;
    background-size:stretch;

}
#featuredPost{
    display:flex;
    width:70%;
    height:400px;
    margin:0 auto;
    background-color:white;
    
}

#featuredPostImage{
    width:70%;
    height:100%;
}

#featuredPostImage img{
    width:100%;
    max-width:100%;
    height:100%;
    
}

#featuredPostText{
    width:30%;
    text-align: left;
    padding-left:10px;
}

#featuredPostTitle{
    font-weight:bold;
}

#readFeatured{
    color:red;
    bottom:5px;
}

/*Blog Posts Container*/
#blogPosts{
    width:70%;
    display:flex;
    margin:0 auto;
    justify-content: space-evenly;
    
}

.post{
    background-color:white;
    width:30%;
    margin:10px;
}

.postImage{
    display:block;
    width:100%;
    padding:0;
    height:200px;
}

.postTitle{
    display:block;
    font-size:1em;
    color: #12bbc2;

}

.postBody{
    text-align:left;
    font-size:0.9em;
    padding:5px;
}

.viewPostLink {
    padding:5px;
    text-decoration: none;
    font-size:1em;
    text-align: right;

}

.viewPostLink a{
    text-decoration: none;
    color: #12bbc2;
    font-size:0.8em;
}
.viewPostLink:visited{
    text-decoration: none;
}
